import React, { useEffect, useState } from 'react'
// @material-ui/core
import { makeStyles } from "@material-ui/core/styles";
// core components
import Card from "components/Card/Card.js";
import CardHeader from "components/Card/CardHeader.js";
import CardBody from "components/Card/CardBody.js";
import MaterialTable from 'material-table';
import { Paper } from '@material-ui/core';

import styles from "assets/jss/material-dashboard-react/views/dashboardStyle.js";

const useStyles = makeStyles(styles);

const raw_columns = [
    { title: 'Date', field: 'date' },
    { title: 'Cell lines', field: 'cell_lines' },
    { title: 'Experiment', field: 'experiment' },
    { title: 'Conditions', field: 'conditions' },
    { title: 'Notes', field: 'notes' },
    { title: 'Results', field: 'results' },
    { title: 'Positions', field: 'positions' },
    { title: 'Student', field: 'owner' },

]


export default function ExpList(props) {
    const classes = useStyles();
    const [tableData, setTableData] = useState([])
    const [columns, setColumns] = React.useState(raw_columns);

    useEffect(() => {
        fetch("http://127.0.0.1:8080/allexperiments/")
            .then(res => res.json())
            .then(
                (result) => {
                    const res = [];

                    Object.keys(result).map((key) => {
                        const rowData = result[key];
                        rowData['exp_id'] = key;
                        res.push(rowData);
                    })
                    setTableData(res)
                },
                (error) => {
                }
            )


  }, [])
  return (
    <div>
      <Card>
        <CardHeader color="success">
          <h4 className={classes.cardTitleWhite}>Available Experiments</h4>
          <p className={classes.cardCategoryWhite}>
            Latest experiment from
          </p>
        </CardHeader>
          <CardBody>
            <MaterialTable
                components={{
                    Container: props => <Paper {...props} elevation={0}/>
                }}
                onRowClick={(event, rowData) => {
                    // <ExpDetailsPage />
                        console.log(rowData);
                        props.history.push('/admin/exp_list/'+rowData['exp_id'])
                    }
                }
                options={{
                    headerStyle: {
                        color: '#4caf50',
                        fontWeight: "300",
                    }
                }}

                title=""
                columns={columns}
                data={tableData}
                editable={{
                    // isEditable: rowData => rowData.owner === "Shaked", // only name(a) rows would be editable
                    onRowUpdate: (newData, oldData) =>
                        new Promise((resolve) => {
                            setTimeout(() => {
                                resolve();
                                //TODO: ADD API TO UPDATE EXPERIMENT
                                if (oldData) {
                                    setTableData((prevState) => {
                                        const data = [...prevState];
                                        data[data.indexOf(oldData)] = newData;
                                        return  data ;
                                    });
                                }
                            }, 600);
                        }),
                    onRowDelete: (oldData) =>
                        new Promise((resolve) => {
                            setTimeout(() => {
                                resolve();
                                //TODO: Add API TO DELETE EXPERIMENT
                                setTableData((prevState) => {
                                    const data = [...prevState];
                                    data.splice(data.indexOf(oldData), 1);
                                    return data;
                                });
                            }, 600);
                        }),
                }}
            />
        </CardBody>
      </Card>
    </div>
  );
}
